import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    isMenuBars: false ,
};

export const setBarsSlice = createSlice({
  name: 'menuBars',
  initialState ,
  reducers: {
    setMenuBars: (state) => {
      state.isMenuBars = !state.isMenuBars;
    },
  }
})

// Action creators are generated for each case reducer function
export const { setMenuBars } = setBarsSlice.actions

export default setBarsSlice.reducer;