import React from 'react'
import Skeleton from 'react-loading-skeleton'
import { BASE_URL } from '../../api/api'
import Theme from '../skeleton/Theme'

export default function StepList({ data , loading }) {
    return (
        <Theme>
            <div className='flex gap-5 items-center mb-5'>
                <div className='step__img'>
                    <img
                        src={`${BASE_URL}${data?.url}`}
                        alt={data?.name}
                    />
                </div>
                <div className='step__body w-3/4'>
                    <h4 className='font-black text-xl'>
                        0{data?.num || <Skeleton width="50px" height="20px"/>}
                    </h4>
                    <div className='step__shape w-full h-[2px] bg-green-100 my-3'></div>
                    <p className='text-white uppercase font-bold'>
                        {data?.title || <Skeleton width="200px"/>}
                    </p>
                </div>
            </div>
        </Theme>
    )
}
