import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Heading, MenuSlider, Navbar, ProductCard } from '../components'
import { Products } from '../mock/products'
import { setBasketBars } from '../redux/reducer/basketBars';
import { getSingleProduct } from '../redux/reducer/basketSlice';
import { productsThunks } from '../redux/reducer/productsSlice';

export default function MenuPage() {
    const dispatch = useDispatch();
    const { productsData, loadingProducts } = useSelector(state => state.products);
    const { activeItem } = useSelector(state => state.setMenu)


    useEffect(() => {
        dispatch(productsThunks(activeItem))
    }, [activeItem])

    const singleProductFunc = (item) => {
        dispatch(getSingleProduct(item))
        dispatch(setBasketBars())
    }

    return (
        <>
            {/* ========== Navbar =========== */}
            <div className="bg-yellow-400 py-20">
                <Navbar color={"dark"} container colorPoint="white" />
            </div>
            {/* ========== Navbar =========== */}


            {/* ==========  Menu start =========== */}
            <div className='menu'>
                <div className='container'>
                    <div className='border-b-2 py-20'>
                        <Heading color="dark" align="start">MENU</Heading>
                    </div>
                </div>

                <MenuSlider />

                <div className='flex flex-wrap gap-1 justify-center products__flex'>
                    {
                        (loadingProducts ? Array.from(new Array(4)) : productsData).map((product, index) => {
                            return (
                                <div onClick={() => singleProductFunc(product)} key={index} className="lg:w-1/5 md:2/5 w-3/4 card__menu">
                                    <ProductCard data={product} />
                                </div>
                            )
                        })
                    }
                </div>

            </div>
            {/* ==========  Menu end =========== */}


        </>
    )
}
