import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const reviewsThunks = createAsyncThunk("getReviews", async () => {
  const { data } = await axios.get(`/api/testimonials`);
  return data?.data;
});

const initialState = {
  reviewsData: null,
  loadingReviews: true,
};

export const reviewsSlice = createSlice({
  name: 'reviews',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(reviewsThunks.pending, (state) => {
        state.loadingReviews = true;
      })
      .addCase(reviewsThunks.fulfilled, (state, action) => {
        state.loadingReviews = false;
        state.reviewsData = action.payload;
      })
      .addCase(reviewsThunks.rejected, (state) => {
        state.loadingReviews = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
// export const reviewsReducer = reviewsSlice.actions

export default reviewsSlice.reducer