import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { isModal } from "../../redux/reducer/basketSlice";
import YellowButton from "../button/YellowButton";

export default function Modal({ ...rest }) {
    const { modalBasket , modalText } = useSelector(state => state.basket);
    const dispatch = useDispatch() ;

    const toggleModal = () => {
        dispatch(isModal())
    }


    return (
        <div className={`modal ${modalBasket ? "active" : ""}`} {...rest}>
            <div className="modal__content">
                <h3 className="text-center font-extrabold text-lg">
                    {modalText}
                </h3>
                <div className="flex gap-2 justify-center flex-wrap py-10 items-center">
                    <Link to="/basket" onClick={toggleModal}>
                        <YellowButton isYellow>
                            Basket
                        </YellowButton>
                    </Link>
                    <Link to="/" onClick={toggleModal}>
                        <YellowButton>
                            Go Home
                        </YellowButton>
                    </Link>
                </div>
            </div>
        </div>
    )
}
