import { LazyLoadImage } from 'react-lazy-load-image-component';
import Skeleton from 'react-loading-skeleton';
import keyImg from '../../assets/icons/key.svg';
import Theme from '../skeleton/Theme';
import img from '../../assets/images/p-item-3.png'
import { BASE_URL } from '../../api/api';

export default function ProductCard({ data }) {
    return (
        <Theme>
            <div className='card product bg-silver-500'>
                <div className='card__img text-center'>
                    <LazyLoadImage
                        alt={data?.name}
                        src={`${BASE_URL}${data?.image}`}
                        effect="blur"
                        className='text-center'
                    />
                    {
                        data?.sub_title ?
                            <>
                                <div className="card__quote bg-black text-white py-5 px-10 rounded-md">
                                    {data?.sub_title}
                                </div>
                            </>
                            :
                            null
                    }
                </div>
                <div className="card__body">
                    <div className='flex flex-wrap justify-between items-center'>
                        <h4 className='text-md font-bold'>
                            {data?.name || <Skeleton width="120px"/>}
                        </h4>
                        <p className='text-green-600'>
                            {data?.time || <Skeleton width="20px"/>}min
                        </p>
                    </div>
                    <p className='text-sm pt-10 pb-15 text-start'>
                        {data?.description || <Skeleton count="2"/>}
                    </p>
                    <h2 className='text-xl text-green-600 font-extrabold text-center '>
                        $ {data?.sizes[0].price || data?.sizes[1].price || data?.sizes[2].price || <Skeleton width="40px"/>}
                    </h2>
                    <div className='card__key'>
                        <img src={keyImg} alt="key" />
                    </div>
                    <div className='card__shape'>
                        +
                    </div>
                </div>
            </div>
        </Theme>
    )
}
