import { useSelector } from 'react-redux';
import basket from '../../assets/icons/basket.svg';

export default function Basket({num=0, color }) {

  return (
    <button className={`${color === "dark" ? "bg-black" : "bg-yellow-600"} rounded-full p-5 text-white relative hover:bg-red-500`}>
        <span>
            <img 
                src={basket}
                alt="Basket"
            />
        </span>
        <div className='basket__shape bg-red-600 text-xs'>
            {num}
        </div>
    </button>
  )
}
