// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectFade, Navigation, Pagination, Autoplay } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import "swiper/css/effect-fade";

import { mainSlider } from '../../mock/main-slider';
import { ViewButton } from '../'
import { Link } from 'react-router-dom';
import Skeleton from 'react-loading-skeleton';
import Theme from '../skeleton/Theme';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { bannersThunks } from '../../redux/reducer/bannersSlice';
import { BASE_URL } from '../../api/api'

export default function MainSlider() {
    const { bannersData, loadingBanners } = useSelector(state => state.banners);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(bannersThunks());
    } , []);

    return (
        <Swiper
            spaceBetween={30}
            navigation={true}
            slidesPerView={1}
            effect={"fade"}
            pagination={{
                type: "fraction",
            }}
            autoplay={{ delay: 5000 }}
            modules={[EffectFade, Navigation, Pagination, Autoplay]}
        >
            {
                (loadingBanners ? Array.from(new Array(2)) : bannersData).map((slide, index) => {
                    return (
                        <SwiperSlide key={index}>
                            <Theme>
                                <div className='flex items-center flex-wrap gap-5 sm:gap-0'>
                                    <div className='sm:w-2/4 w-full'>
                                        <p>
                                            New In Menu
                                        </p>
                                        <h2 className='text-2xl sm:text-3xl leading-15 font-black text-bg pt-20 pb-50 '>
                                            {slide?.title || <Skeleton />}
                                        </h2>
                                        <Link to={'/menu'}>
                                            <ViewButton styled={"30"}>
                                                View Menu
                                            </ViewButton>
                                        </Link>
                                    </div>
                                    <div className='sm:w-2/4 w-full'>
                                        <div className='main__img text-center relative'>
                                            <LazyLoadImage
                                                alt={slide?.title}
                                                src={`${BASE_URL}${slide?.image}`}
                                                // src={mainSlider?.imageUrl}
                                                className="image"
                                                effect="blur"
                                            />
                                            <div className="main__shape">
                                                <h4 className='text-red-500 text-lg sm:text-xl font-extrabold'>${slide?.price}</h4>
                                                <p className='bg-yellow-500 px-5 sm:px-10 rounded-xl text-white'>super price</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Theme>
                        </SwiperSlide>
                    )
                })
            }
        </Swiper>
    )
}
