import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

export const getAdditionalThunks = createAsyncThunk("getAdditional", async () => {
  const { data } = await axios.get(`/api/additional-products`);
  return data?.data;
});

const initialState = {
  singleProduct: null,
  additionalData: null,
  loadingAdditional: true,
  modalBasket: false,
  modalText: "",
  sizeActive: 1,
  addActive: 0,
  basketData: [] ,
  product: null ,
};

export const basketSlice = createSlice({
  name: 'basket',
  initialState,
  reducers: {
    getSingleProduct: (state, action) => {
      state.singleProduct = action.payload;
    },

    // basket bar counter
    increment: (state, action) => {
      state.product = action.payload ;
      state.product.count += 1;
      const currentProduct = state.singleProduct?.sizes.filter(item => item.id === state.sizeActive);
      const total = state.singleProduct.count * currentProduct[0].price;
      state.singleProduct.withOutTotal = total
    },
    decrement: (state, action) => {
      if (state.singleProduct.count === 0) {
        state.singleProduct.count = 0;
      } else {
        state.singleProduct.count = state.singleProduct.count -= 1
        const currentProduct = state.singleProduct?.sizes.filter(item => item.id === state.sizeActive);
        const total = state.singleProduct.count * currentProduct[0].price;
        state.singleProduct.withOutTotal = total;
      }
    },
    setAddActive: (state, action) => {
      state.singleProduct.withTotal = 0;
      state.addActive = action.payload.id;
      state.singleProduct.total = (state.singleProduct.withTotal + action.payload.price) + state.singleProduct.withOutTotal;
    },


    isModal: (state, action) => {
      state.modalBasket = !state.modalBasket;
      state.modalText = action.payload;
    },
    setSizeActive: (state, action) => {
      state.sizeActive = action.payload;
    },

    // in the basket
    pushBasketData: (state, action) => {
      // state.basketData= state.basketData.push(...state.basketData , action.payload);
      state.basketData = [...state.basketData , action.payload];
      // localStorage.clear();
      localStorage.setItem("basket", JSON.stringify(state.basketData));
    },

    getBasketData: (state) => {
      state.basketData = JSON.parse(localStorage.getItem("basket"));
    },

    // in the basket counter


  },
  extraReducers: (builder) => {
    builder
      .addCase(getAdditionalThunks.pending, (state) => {
        state.loadingAdditional = true;
      })
      .addCase(getAdditionalThunks.fulfilled, (state, action) => {
        state.loadingAdditional = false;
        state.additionalData = action.payload;
      })
      .addCase(getAdditionalThunks.rejected, (state) => {
        state.loadingAdditional = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
export const { 
  getSingleProduct, 
  increment, decrement, 
  isModal, 
  pushBasketData, getBasketData,
  setSizeActive, setAddActive 
} = basketSlice.actions

export default basketSlice.reducer;