import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const popularProductsThunks = createAsyncThunk("getPopulars", async () => {
  const { data } = await axios.get(`/api/popular-recipes`);
  return data?.data;
});

const initialState = {
  popularsData: null,
  loadingPopulars: true,
};

export const popularSlice = createSlice({
  name: 'populars',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(popularProductsThunks.pending, (state) => {
        state.loadingPopulars = true;
      })
      .addCase(popularProductsThunks.fulfilled, (state, action) => {
        state.loadingPopulars = false;
        state.popularsData = action.payload;
      })
      .addCase(popularProductsThunks.rejected, (state) => {
        state.loadingPopulars = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
// export const popularReducer = popularSlice.actions

export default popularSlice.reducer