import React from 'react'

export default function YellowButton({children , isYellow }) {
  return (
    <button className={`hover:bg-yellow-600 hover:text-white border-2 border-yellow-600 rounded-md px-40 py-10 font-extrabold text-yellow-600 ${isYellow ? "bg-dark-500 text-white border-dark-500 hover:bg-gray-900" : ""}`}>
        {children}
    </button>
  )
}
