// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from "swiper";
import { ReviewsInfo } from '../../mock/review'

// Import Swiper styles
import ReviewCard from '../card/ReviewCard';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { reviewsThunks } from '../../redux/reducer/reviewsSlice';

export default function ReviewsSlider() {
    const { reviewsData, loadingReviews } = useSelector(state => state.reviews);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(reviewsThunks())
    } , [])

    return (
        <Swiper
            spaceBetween={30}
            navigation={true}
            slidesPerView={3}
            loop={true}
            autoplay={{ delay: 5000 }}
            modules={[Navigation, Autoplay]}
            className="py-50"
            breakpoints={{
                0: {
                    slidesPerView: 1,
                    spaceBetween: -50,
                },
                350: {
                    slidesPerView: 1,
                    spaceBetween: -100,
                    loop: false ,
                },
                480: {
                    slidesPerView: 2,
                    spaceBetween: 40,
                },
                900: {
                    slidesPerView: 3,
                    spaceBetween: 50,
                },
            }}
        >
            {
                (loadingReviews ? Array.from(new Array(3)) : reviewsData).map((product, index) => {
                    return (
                        <SwiperSlide key={index}>
                            <ReviewCard data={product} loading={loadingReviews} />
                        </SwiperSlide>
                    )
                })
            }
        </Swiper>
    )
}
