import React from 'react'
import { useDispatch } from 'react-redux'
import { decrement, increment } from '../../redux/reducer/basketSlice';

export default function Counter({ current = 0 , product }) {
    const dispatch = useDispatch();

    return (
        <div className='flex gap-1 items-center justify-center'>
            <button
                className={`rounded-full ${current === 0 ? "bg-red-200" : "bg-red-500"} w-7 h-7 text-white`}
                onClick={() => dispatch(decrement(product))}
            >
                -
            </button>
            <p className='bg-gray-100 py-5 px-20 rounded-md'>
                {current}
            </p>
            <button
                className={`rounded-full bg-red-500 w-7 h-7 text-white`}
                onClick={() => dispatch(increment(product))}
            >
                +
            </button>
        </div>
    )
}
