import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const menuThunks = createAsyncThunk("getMenu", async () => {
  const { data } = await axios.get(`/api/categories`);
  return data?.data;
});

const initialState = {
  menuData: null,
  loadingMenu: true,
};

export const menuSlice = createSlice({
  name: 'menu',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(menuThunks.pending, (state) => {
        state.loadingMenu = true;
      })
      .addCase(menuThunks.fulfilled, (state, action) => {
        state.loadingMenu = false;
        state.menuData = action.payload;
      })
      .addCase(menuThunks.rejected, (state) => {
        state.loadingMenu = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
// export const reviewsReducer = reviewsSlice.actions

export default menuSlice.reducer