import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const settingsThunks = createAsyncThunk("getSettings", async () => {
    const { data } = await axios.get(`/api/settings`);
    return data?.data;
});

const initialState = {
    settingsData: null,
    loadingSettings: true,
};

export const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(settingsThunks.pending, (state) => {
                state.loadingSettings = true ;
            })
            .addCase(settingsThunks.fulfilled, (state, action) => {
                state.loadingSettings = false ;
                state.settingsData = action.payload;
             })
            .addCase(settingsThunks.rejected, (state) => {
                state.loadingSettings = true ;
             })
            .addDefaultCase((state, action) => { })
    },
})

// Action creators are generated for each case reducer function
// export const settingsReducer = settingsSlice.actions

export default settingsSlice.reducer