import React, { useEffect } from 'react'
import Skeleton from 'react-loading-skeleton';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Heading, Input, Navbar, Textarea, ViewButton } from '../components'
import { settingsThunks } from '../redux/reducer/settings';

export default function Partnership() {
    const { settingsData, loadingSettings } = useSelector(state => state.settings);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(settingsThunks());
    }, []);

    return (
        <>
            {/* ========== Navbar =========== */}
            <div className="bg-yellow-400 py-20">
                <Navbar color={"dark"} container colorPoint="white" />
            </div>
            {/* ========== Navbar =========== */}
            {/* ==========  Menu start =========== */}
            <div className='container'>
                <div className='border-b-2 py-20'>
                    <Heading color="dark" align="start">Partnership</Heading>
                </div>

                <div className='py-30'>
                    <p className='text-md'>
                        {
                            settingsData?.partnership_text || <Skeleton count={3}/>
                        }
                    </p>
                </div>

                <form className='py-30'>

                    <p className='font-bold text-md'>
                        Leave a Request for Partnership
                    </p>

                    <div className='flex flex-wrap  w-full md:w-3/4 gap-y-2 py-30'>

                        <div className='w-full pr-0 sm:pr-20 sm:w-2/4'>
                            <Input type="text" placeholder="First Name" state="" setState="test" />
                        </div>
                        <div className='w-full pr-0 sm:pr-20 sm:w-2/4'>
                            <Input type="text" placeholder="Last Name" state="" setState="test" />
                        </div>
                        <div className='w-full pr-0 sm:pr-20 sm:w-2/4'>
                            <Input type="tel" placeholder="Phone" state="" setState="test" />
                        </div>
                        <div className='w-full pr-0 sm:pr-20 sm:w-2/4'>
                            <Input type="email" placeholder="Email" state="" setState="test" />
                        </div>
                        <Textarea placeholder="How can we help?" state="" setState="" />


                    </div>

                    <ViewButton styled="30">
                        Submit
                    </ViewButton>

                </form>

            </div>
            {/* ==========  Menu end =========== */}

        </>
    )
}
