import { useSelector } from 'react-redux';
import { BASE_URL } from '../../api/api';
import { SocialsItems } from '../../mock/socialsItem'

export default function Socials() {
    const { settingsData, loadingSettings } = useSelector(state => state.settings);

    return (
        <div className='pt-30 flex flex-wrap gap-2'>
            {
                settingsData?.socials?.length > 0 &&
                settingsData?.socials.map((item, index) => {
                    return (
                        <a href={item?.url} key={index} target="_blank">
                            <div className='social__item hover:bg-red-500 bg-white flex justify-center items-center' >
                                <img src={`${BASE_URL}${item?.icon}`} alt={item?.name} className="img" />
                            </div>
                        </a>
                    )
                })
            }
        </div>
    )
}
