import { MenuProduct } from '../../mock/menu-product';
import { useDispatch, useSelector } from 'react-redux';
import { setMenuActive } from '../../redux/reducer/SetMenu';
import { useEffect, useState } from 'react';
import { menuThunks } from '../../redux/reducer/menuSlice';
import { BASE_URL } from '../../api/api';
import Skeleton from 'react-loading-skeleton';
import Theme from '../skeleton/Theme';
import { productsThunks } from '../../redux/reducer/productsSlice';

export default function MenuSlider() {
    const dispatch = useDispatch();
    const { activeItem } = useSelector(state => state.setMenu)
    const { menuData, loadingMenu } = useSelector(state => state.menu);

    const categoryChangeListener = (id) => {
        dispatch(setMenuActive(id));
    }

    useEffect(() => {
        dispatch(menuThunks())
    }, [])

    return (
        <Theme>
            <div className='bg-yellow-600 my-10 menu__slider'>
                <div className="container">
                    <div className='flex gap-10 items-center justify-start lg:justify-center menu__slider--flex'>
                        {
                            (loadingMenu ? Array.from(new Array(4)) : menuData).map(
                                (menu, index) => {
                                    return (
                                        <div
                                            className={`menu__item flex gap-2 justify-center items-center py-20 cursor-pointer ${menu?.id === activeItem ? "active" : ""}`}
                                            key={index}
                                            onClick={() => categoryChangeListener(menu?.id)}
                                        >
                                            <div className='menu__img'>
                                                <img src={`${BASE_URL}${menu?.url}`} alt="image" />
                                            </div>
                                            <div className='text-green-100 text-md'>
                                                {menu?.name || <Skeleton width="100px"/>}
                                            </div>
                                        </div>
                                    )
                                }
                            )
                        }
                    </div>
                </div>
            </div>
        </Theme>
    )
}
