import { configureStore } from '@reduxjs/toolkit'
import bannersSlice from './reducer/bannersSlice'
import popularSlice from './reducer/popularSlice'
import reviewsSlice from './reducer/reviewsSlice'
import productsSlice from './reducer/productsSlice'
import menuSlice from './reducer/menuSlice'
import setMenu from './reducer/SetMenu'
import settingsSlice from './reducer/settings'
import setBarsSlice from './reducer/menuBars'
import basketBarsSlice from './reducer/basketBars'
import basketSlice from './reducer/basketSlice'

export default configureStore({
  reducer: {
    settings: settingsSlice,
    banners: bannersSlice,
    populars: popularSlice,
    products: productsSlice,
    menu: menuSlice,
    reviews: reviewsSlice,
    setMenu: setMenu,
    menuBars: setBarsSlice,
    basketBars: basketBarsSlice,
    basket: basketSlice,
  }
})