import React from 'react'
import Skeleton from 'react-loading-skeleton';
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom';
import { menuItems } from '../../mock/menu-item';
import { setMenuBars } from '../../redux/reducer/menuBars';
// import { setMenuBars } from '../../redux/reducer/menuBars';
import ViewButton from '../button/ViewButton';

export default function MenuBars() {
    const { isMenuBars } = useSelector(state => state.menuBars);
    const dispatch = useDispatch();


    return (
        <div className={`menu__bars ${isMenuBars ? "active" : ""}`}>

            <div className='flex justify-center'>
                <ViewButton onClick={() => dispatch(setMenuBars())}>
                    Close
                </ViewButton>
            </div>

            <ul className='py-30'>
                {
                    menuItems.length > 0 &&
                    <>
                        {
                            menuItems.map((item, index) => {
                                return (
                                    <Link
                                        to={item?.url}
                                        key={index}
                                        onClick={() => dispatch(setMenuBars())}
                                    >
                                        <li
                                            className={`hover:text-red-500 font-black uppercase text-sm text-bg mb-5`}
                                        >
                                            {item?.name || <Skeleton width="100px" />}
                                        </li>
                                    </Link>
                                )
                            })
                        }
                    </>
                }
            </ul>

        </div>
    )
}
