import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const bannersThunks = createAsyncThunk("getBannersData", async () => {
  const { data } = await axios.get(`/api/carousels`);
  return data?.data;
});

const initialState = {
  bannersData: null,
  loadingBanners: true,
};

export const bannersSlice = createSlice({
  name: 'banners',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(bannersThunks.pending, (state) => {
        state.loadingBanners = true;
      })
      .addCase(bannersThunks.fulfilled, (state, action) => {
        state.loadingBanners = false;
        state.bannersData = action.payload;
      })
      .addCase(bannersThunks.rejected, (state) => {
        state.loadingBanners = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
// export const bannerReducer = bannersSlice.actions

export default bannersSlice.reducer