import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../api/api'

// thunk function
export const productsThunks = createAsyncThunk("getProducts", async (id) => {
  const { data } = await axios.get(`/api/category/${id}`);
  return data?.data;
});

const initialState = {
  productsData: null,
  loadingProducts: true,
};

export const productsSlice = createSlice({
  name: 'products',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(productsThunks.pending, (state) => {
        state.loadingProducts = true;
      })
      .addCase(productsThunks.fulfilled, (state, action) => {
        state.loadingProducts = false;
        state.productsData = action.payload;
      })
      .addCase(productsThunks.rejected, (state) => {
        state.loadingProducts = true;
      })
      .addDefaultCase((state, action) => { })
  },
})

// Action creators are generated for each case reducer function
// export const reviewsReducer = reviewsSlice.actions

export default productsSlice.reducer