// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from "swiper";
import { Products } from '../../mock/products'

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import PopularCard from '../card/PopularCard';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { popularProductsThunks } from '../../redux/reducer/popularSlice';
import { getSingleProduct } from '../../redux/reducer/basketSlice';
import { setBasketBars } from '../../redux/reducer/basketBars';

export default function PopularSlider() {
    const { popularsData, loadingPopulars } = useSelector(state => state.populars);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(popularProductsThunks());
    }, []);

    const singleProductFunc = (item) => {
        dispatch(getSingleProduct(item))
        dispatch(setBasketBars())
    }

    return (
        <Swiper
            spaceBetween={30}
            navigation={true}
            // slidesPerView={3}
            centeredSlides={true}
            loop={true}
            autoplay={{ delay: 5000 }}
            modules={[Navigation, Autoplay]}
            className="py-50"
            breakpoints={{
                0: {
                    slidesPerView: 1,
                    spaceBetween: -50,
                },
                350: {
                    slidesPerView: 1,
                    spaceBetween: -100,
                    centeredSlides: false,
                    loop:false ,
                },
                480: {
                    slidesPerView: 2,
                    spaceBetween: 40,
                },
                900: {
                    slidesPerView: 3,
                    spaceBetween: 50,
                },
            }}
        >
            {
                (loadingPopulars ? Array.from(new Array(4)) : popularsData).map((product, index) => {
                    return (
                        <SwiperSlide key={index}>
                            <div onClick={() => singleProductFunc(product)}>
                                <PopularCard data={product} />
                            </div>
                        </SwiperSlide>
                    )
                })
            }
        </Swiper>
    )
}
