import React, { useEffect } from 'react'
import { MainSlider, Navbar, PopularSlider, Heading, StepList, MenuSlider, MenuCards, YellowButton, ViewButton, ReviewsSlider } from '../components'
import StpesImg from '../assets/images/step-main.png';
import { Link } from 'react-router-dom';
import branchImg from '../assets/images/branch.png'
// import ReactPlayer from 'react-player';
// import videoPl from '../assets/video/video.mp4'
import shapeImgFirst from '../assets/images/absolute.png'
import aboutsImg from '../assets/images/abouts.png'
import { useDispatch, useSelector } from 'react-redux';
import { settingsThunks } from '../redux/reducer/settings';
import "swiper/css";
import "swiper/css/navigation";
import 'react-lazy-load-image-component/src/effects/blur.css';


export default function Home() {
    const dispatch = useDispatch();
    const { settingsData, loadingSettings } = useSelector(state => state.settings);


    useEffect(() => {
        dispatch(settingsThunks());
    }, []);

    return (
        <>

            {/* ========== Main Slider =========== */}
            <div className="main bg-yellow-600">
                <div className="container">
                    <Navbar colorPoint="dark" />

                    <MainSlider/>

                </div>
            </div>
            {/* ========== Main Slider =========== */}

            {/* ==========  One Bg Start =========== */}
            <div className='one__bg py-20 md:py-50'>


                {/* ==========  Popular Recipes start =========== */}
                <div className='container'>

                    <Heading color="dark" align="center">Popular Recipes</Heading>
                    <PopularSlider/>

                </div>
                {/* ==========  Popular Recipes end =========== */}


                {/* ==========  Why Are We? start =========== */}
                <div className='why bg-yellow-600 relative z-0 py-50 my-10 overflow-hidden'>
                    <div className="container">

                        <Heading color="white" align="center">
                            Why Are We?
                        </Heading>

                        <div className='w-full md:w-2/4 py-30'>
                            {
                                (loadingSettings ? Array.from(new Array(3)) : settingsData?.advantages).map((step, index) => {
                                    return (
                                        <StepList data={step} key={index} loading={loadingSettings} />
                                    )
                                })
                            }
                        </div>

                        <div className='why__shape'>
                            <img src={StpesImg} alt="shape" />
                        </div>

                    </div>
                </div>
                {/* ==========  Why Are We? end =========== */}


                {/* ==========  Menu start =========== */}
                <div className='menu'>
                    <Heading color="dark" align="center">Menu</Heading>

                    <MenuSlider />
                    <MenuCards />
                    <MenuCards />
                    <div className='flex justify-center py-20'>
                        <Link to="/menu">
                            <YellowButton>View All</YellowButton>
                        </Link>
                    </div>

                </div>
                {/* ==========  Menu end =========== */}


            </div>
            {/* ==========  One Bg End =========== */}


            <div className='container' id='branches'>

                {/* ==========  Our Branch Start =========== */}
                <Heading color="dark" align="center">Our Branch</Heading>
                <div className='flex flex-wrap gap-2 md:gap-0 py-30'>
                    <div className='w-full md:w-2/4 branch pr-1'>
                        <div className='bg-red-100 p-15 rounded-xl'>
                            <div className="overflow-hidden rounded-xl">
                                <img src={branchImg} alt="about" />
                            </div>
                            <div className='py-10 px-15'>
                                <p className='text-xs'>
                                    Adress:
                                </p>
                                <h3 className='text-md font-bold'>
                                    {settingsData?.address}
                                </h3>
                            </div>
                            <div className='py-10 px-15'>
                                <p className='text-xs'>
                                    Call:
                                </p>
                                <h3 className='text-md font-bold'>
                                    <a href={`tel:${settingsData?.phone}`}>
                                        {settingsData?.phone}
                                    </a>
                                </h3>
                            </div>
                            <a href={`${settingsData?.map}`} target="_blank">
                                <ViewButton>
                                    View in map
                                </ViewButton>
                            </a>
                        </div>
                    </div>
                    <div className='w-full md:w-2/4 map'>
                        {/* <iframe src="https://yandex.com/map-widget/v1/?um=constructor%3A6350291360198dfbd475cf0c0de68e95e57eb115c370b2852d2cc2f32d567a11&amp;source=constructor" title='title'></iframe> */}

                       <iframe width="100%" height="100%" id="gmap_canvas" src={`https://maps.google.com/maps?q=${settingsData?.address}&t=&z=12&ie=UTF8&iwloc=&output=embed`}></iframe>
                    </div>
                </div>
                {/* ==========  Our Branch End =========== */}


                {/* ==========  Video Start =========== */}
                {/* <div className='player'>
                    <ReactPlayer
                        className="react-player"
                        url={videoPl}
                        playing
                        muted
                        loop
                        controls
                        playIcon={<div className="branch__video__circle"></div>}
                    />
                </div> */}
                {/* ==========  Video End =========== */}


            </div>

            {/* ==========  Reviews Start =========== */}
            <div className='reviews py-20'>
                <Heading color="dark" align="center">Reviews</Heading>

                <div className='reviews__content my-2'>

                    <div className='container'>
                        <ReviewsSlider/>
                    </div>

                    <div className='reviews__content--shape'>
                        <img src={shapeImgFirst} alt="shape first" />
                    </div>
                    <div className='reviews__content--shape2 hidden md:block'>
                        <img src={shapeImgFirst} alt="shape second" />
                    </div>

                </div>

            </div>
            {/* ==========  Reviews End =========== */}


            {/* ==========  About us Start =========== */}
            <div className='abouts py-50'>
                <Heading color="dark" align="center">About Us</Heading>

                <div className='abouts__content'>
                    <div className='container'>
                        <div className='flex flex-wrap justify-between items-end'>
                            <div className='w-full md:w-2/4 py-30 text-white'>
                                <p>
                                    Welcome to Venice Pizzeria, your one-stop destination for all things pizza! We are a passionate team of foodies dedicated to providing you with the best pizza experience possible. With a wide range of toppings and crust options, we guarantee there's something for everyone.


                                    Our mission : To bring the joy of pizza to your doorsteps, no matter where you are.

                                    We use only the freshest ingredients and carefully crafted recipes to ensure that every bite is bursting with flavor. Whether you're in the mood for a classic Margarita, a spicy pepperoni, or a unique creation of your own, we've got you covered.


                                    Our easy-to-use online ordering system makes it simple to choose your perfect pizza, select a delivery or pickup time, and track your order in real-time. Our delivery drivers are friendly and reliable, ensuring that your pizza arrives hot and fresh, straight from our oven to your table.
                                    At Venice Pizzeria, we believe in giving back to our community. That's why we work with local farmers and suppliers to source our ingredients and support the local economy. We also donate a portion of our profits to local charities and organizations, so you can feel good about your pizza purchase.
                                    Thank you for choosing Venice Pizzeria, we can't wait to serve you the best pizza you've ever had!
                                    <br />
                                    <br />
                                    <b><i>Order now and taste the difference for yourself.</i></b>
                                </p>
                            </div>
                            <div className='w-full md:w-2/4 pl-30'>
                                <div className='abouts__img'>
                                    <img src={aboutsImg} alt="abouts" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            {/* ==========  About us End =========== */}


        </>
    )
}
