import React from 'react'
import { Counter, Heading, Navbar } from '../components'
import { useDispatch, useSelector } from 'react-redux'
import { BASE_URL } from '../api/api';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { useState } from 'react';
import { useEffect } from 'react';
// import { getBasketData } from '../redux/reducer/basketSlice';

export default function BasketPage() {
  const { basketData } = useSelector(state => state.basket);
  const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch(getBasketData())
  // }, [])



  return (
    <>
      {/* ========== Navbar =========== */}
      <div className="bg-yellow-400 py-20">
        <Navbar color={"dark"} container colorPoint="white" />
      </div>
      {/* ========== Navbar =========== */}

      {/* ==========  Menu start =========== */}
      <div className='menu'>
        <div className='container'>
          <div className='border-b-2 py-20'>
            <Heading color="dark" align="start">Basket</Heading>
          </div>

          <div className='overflow-x-auto'>
            <table className='w-full text-center table'>
              <thead>
                <tr>
                  <td className='py-20 font-black text-start w-2/5'>Product</td>
                  <th>Size</th>
                  <th>Number</th>
                  <th>Price</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                {

                  basketData?.length > 0 ?
                    basketData.map((item, index) => {
                      return (
                        <tr key={index}>
                          <td className='flex items-center'>
                            <section className='table__img'>
                              <LazyLoadImage
                                alt={item?.name}
                                src={`${BASE_URL}${item?.image}`}
                                effect="blur"
                                className='text-center'
                              />
                            </section>
                            <section className='text-start'>
                              <h3 className='font-black text-base md:text-md'>{item?.name}</h3>
                              <p className='text-sm md:text-base'>
                                {item?.description}
                              </p>
                            </section>
                          </td>
                          <td>
                            {item?.size ? item?.size[0].size : ""} sm
                          </td>
                          <td>
                            <div className='flex gap-1 items-center justify-center'>
                              <button
                                className={`rounded-full ${item?.count === 0 ? "bg-red-200" : "bg-red-500"} w-7 h-7 text-white`}
                                onClick={""}
                              >
                                -
                              </button>
                              <p className='bg-gray-100 py-5 px-20 rounded-md'>
                                {item?.count}
                              </p>
                              <button
                                className={`rounded-full bg-red-500 w-7 h-7 text-white`}
                                onClick={""}
                              >
                                +
                              </button>
                            </div>
                          </td>
                          <td>
                            ${item?.size ? item?.size[0].price : ""}
                          </td>
                          <td>
                            {item?.total}
                          </td>
                        </tr>
                      )
                    })
                    :
                    <td colSpan={5}>
                      <p className='py-20 text-red-600 font-bold'>
                        No Date
                      </p>
                    </td>

                }
              </tbody>
            </table>
          </div>

        </div>


      </div>
      {/* ==========  Menu end =========== */}

    </>
  )
}
