import { Link } from 'react-router-dom';
import logo from '../../assets/images/logo.png';
import { menuItems } from '../../mock/menu-item'
import Button from '../button/Button';
import phone from '../../assets/icons/phone.svg';
import Basket from '../button/Basket';
import MenuIcon from '../button/MenuIcon';
import { BASE_URL } from '../../api/api';
import Skeleton from 'react-loading-skeleton';
import Theme from '../skeleton/Theme';
import { setMenuBars } from '../../redux/reducer/menuBars';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { settingsThunks } from '../../redux/reducer/settings';

export default function Navbar({ colorPoint, container }) {
    const { settingsData, loadingSettings } = useSelector(state => state.settings);
    const { basketData } = useSelector(state => state.basket);

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(settingsThunks());
    }, [])

    return (
        <Theme>
            <div className={`${container ? "container" : ""}`}>
                <div className='flex gap-10 py-10 justify-between items-center'>
                    <div className='nav__logo'>
                        <Link to='/'>
                            {
                                settingsData?.logos && settingsData?.logos[0] ?
                                    <img
                                        src={`${BASE_URL}${settingsData?.logos[0]?.url}`}
                                        alt={settingsData[0]?.place}
                                        className="logo"
                                    />
                                    :
                                    <img
                                        src={logo}
                                        alt="logo"
                                        className="logo"
                                    />
                            }
                        </Link>
                    </div>
                    <div className="nav__menu pl-40 hidden lg:block">
                        <ul className='flex gap-10'>
                            {
                                menuItems.length > 0 &&
                                <>
                                    {
                                        menuItems.map((item, index) => {
                                            return (
                                                <Link to={item?.url} key={index}>
                                                    <li
                                                        className={`hover:text-red-500 font-semibold uppercase text-sm ${colorPoint === "dark" ? "text-bg" : "text-white"} `}
                                                    >
                                                        {item?.name || <Skeleton width="100px" />}
                                                    </li>
                                                </Link>
                                            )
                                        })
                                    }
                                </>
                            }
                        </ul>
                    </div>
                    <div className="nav__order flex items-center gap-2 md:gap-10">
                        <a href={`tel:${settingsData?.phone}`} className='hidden md:block'>
                            <Button color={colorPoint}>
                                <span>
                                    <img src={phone} alt="phone" />
                                </span>
                                <span>
                                    {
                                        settingsData?.phone || <Skeleton width="100px" />
                                    }
                                </span>
                            </Button>
                        </a>
                        <Link to="/basket">
                            <Basket color={colorPoint} num={basketData?.length}/>
                        </Link>
                        <div className='block lg:hidden'>
                            <MenuIcon onClick={() => dispatch(setMenuBars())} />
                        </div>
                    </div>
                </div>
            </div>
        </Theme>
    )
}
