import React, { useEffect, useState } from 'react'
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Skeleton from 'react-loading-skeleton';
import { useDispatch, useSelector } from 'react-redux'
import { BASE_URL } from '../../api/api';
import { getAdditionalThunks, setAddActive } from '../../redux/reducer/basketSlice';
import Theme from '../skeleton/Theme';
import keyImg from '../../assets/icons/key.svg';

export default function AdditionalCard({ getCurrentAdditional }) {
    const { additionalData, loadingAdditional , addActive } = useSelector(state => state.basket);
    const dispatch = useDispatch();


    const ActiveAdditional = (data) => {
        dispatch(setAddActive(data))
        getCurrentAdditional(data);
    }

    useEffect(() => {
        dispatch(getAdditionalThunks());
    }, []);

    return (
        <div className='flex flex-wrap p-30 pb-40 gap-y-10 gap-x-2 bg-gray-100 mt-10'>

            {
                (loadingAdditional || additionalData.length === 0 ? Array.from(new Array(3)) : additionalData).map((data, index) => {
                    return (
                        <div
                            className={`card product additional bg-silver-500 ${addActive === data?.id ? "active" : ""}`}
                            onClick={() => ActiveAdditional(data)}
                            key={index}
                        >
                            <Theme>
                                <div className='card__img text-center'>
                                    <LazyLoadImage
                                        alt={data?.name}
                                        src={`${BASE_URL}${data?.image}`}
                                        effect="blur"
                                        className='text-center'
                                    />
                                </div>
                                <div className="card__body">
                                    <div className='flex flex-wrap justify-between items-center'>
                                        <h4 className='text-base font-bold'>
                                            {data?.name || <Skeleton width="120px" />}
                                        </h4>
                                    </div>
                                    <h2 className='text-base text-green-600 font-extrabold text-center '>
                                        $ {data?.price || <Skeleton width="40px" />}
                                    </h2>
                                    <div className='card__key'>
                                        <img src={keyImg} alt="key" />
                                    </div>
                                    <div className='card__shape'>
                                        +
                                    </div>
                                </div>
                            </Theme>
                        </div>
                    )
                })
            }

        </div>
    )
}
