import { useDispatch, useSelector } from 'react-redux'
import Close from '../button/Close'
import { Sizes } from '../../mock/size'
import { useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { setBasketBars } from '../../redux/reducer/basketBars';
import Skeleton from 'react-loading-skeleton';
import Theme from '../skeleton/Theme';
import { BASE_URL } from '../../api/api';
import Counter from '../form/Counter';
import AdditionalCard from '../card/AdditionalCard';
import ViewButton from '../button/ViewButton';
import { isModal, pushBasketData, setSizeActive } from '../../redux/reducer/basketSlice';

export default function BasketBars() {
    const { isBasketBars } = useSelector(state => state.basketBars);
    const { singleProduct , sizeActive } = useSelector(state => state.basket);
    const dispatch = useDispatch();
    const [additionCard , setAdditionCard] = useState(null);

    // get current additional product
    const getCurrentAdditional = (item) => {
        setAdditionCard(item);
    };

    // all current product 
    const currentProgress = () => {
        dispatch(setBasketBars())
        dispatch(isModal("What do you want to do ?"));

        // to collect current product;
        const currentProduct = {
            id: singleProduct?.id,
            name: singleProduct?.name,
            description: singleProduct?.description,
            total: singleProduct?.total,
            size: singleProduct?.sizes.filter(item => item.id === sizeActive),
            count : singleProduct?.count,
            image : singleProduct?.image,
            additionalProduct : additionCard
        }

        dispatch(pushBasketData(currentProduct));
        console.log(currentProduct)




    }

    return (
        <div className={`basket__bars ${isBasketBars ? "active" : ""}`}>
            <Theme>
                <div className="basket__content">

                    <div className="basket__header flex px-20 py-10 justify-between items-center">

                        <h3 className='text-lg font-black'>
                            {singleProduct?.name || <Skeleton />}
                        </h3>

                        <Close onClick={() => dispatch(setBasketBars())} />

                    </div>

                    <div className="flex border-t-2">
                        {
                            Sizes.length > 0 &&
                            Sizes.map((size, index) => {
                                return (
                                    <div
                                        className={`basket__item cursor-pointer py-2 w-1/3 text-center border-r-2 ${sizeActive === size?.id ? "border-b-2 border-green-800 text-green-600 font-black" : ""}`}
                                        key={index}
                                        onClick={() => dispatch(setSizeActive(size?.id))}
                                    >
                                        <p className={`text-green-500`}>{size.name}</p>
                                    </div>
                                )
                            })
                        }
                    </div>

                    <div className='card__img text-center relative p-20'>
                        <LazyLoadImage
                            alt="data"
                            src={`${BASE_URL}${singleProduct?.image}`}
                            effect="blur"
                            className='text-center'
                        />
                        {
                            <div className="card__quote bg-green-600 text-white py-5 px-10 rounded-md">
                                {
                                    singleProduct?.sizes.map((item, index) => {
                                        return (
                                            <p key={index}>
                                                {
                                                    item.id === sizeActive && <>{item?.size}sm</>
                                                }
                                            </p>
                                        )
                                    })
                                }
                            </div>
                        }
                    </div>

                    <div className="flex items-end justify-between px-20">

                        <div className='w-2/4'>
                            {singleProduct?.description}
                        </div>

                        <Counter current={singleProduct?.count} product={singleProduct}/>

                    </div>

                    <AdditionalCard getCurrentAdditional={getCurrentAdditional} />

                    <ViewButton
                        style={{
                            width: "100%",
                            borderRadius: "0px"
                        }}
                        onClick={() => currentProgress()}
                    >
                        Done
                        <span className='px-20'>
                            (${singleProduct?.total})
                        </span>
                    </ViewButton>

                </div>
            </Theme>
        </div>
    )
}
