// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from "swiper";
import { Products } from '../../mock/products'

// Import Swiper styles
import ProductCard from '../card/ProductCard';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { productsThunks } from '../../redux/reducer/productsSlice';
import { getSingleProduct } from '../../redux/reducer/basketSlice';
import { setBasketBars } from '../../redux/reducer/basketBars';

export default function MenuCards() {
    const dispatch = useDispatch();
    const { productsData, loadingProducts } = useSelector(state => state.products);
    const { activeItem } = useSelector(state => state.setMenu)



    useEffect(() => {
        dispatch(productsThunks(activeItem))
    }, [activeItem])

    const singleProductFunc = (item) => {
        dispatch(getSingleProduct(item))
        dispatch(setBasketBars())
    }

    return (
        <div className='container-full'>
            <Swiper
                spaceBetween={30}
                navigation={true}
                // centeredSlides={true}
                // roundLengths= {true}
                // loop={true}
                // loopAdditionalSlides={100}
                // slidesPerView={1}
                autoplay={{
                    delay: 3000,
                    disableOnInteraction: false
                }}
                modules={[Autoplay]}
                className="py-50 menuCards"
                breakpoints={{
                    0: {
                        slidesPerView: 1,
                        spaceBetween: -50,
                    },
                    350: {
                        slidesPerView: 1,
                        spaceBetween: -100,
                        centeredSlides: false,
                        loop: false,
                    },
                    480: {
                        slidesPerView: 2,
                        spaceBetween: 20,
                    },
                    760: {
                        slidesPerView: 3,
                        spaceBetween: 40,
                    },
                    1100: {
                        slidesPerView: 4,
                        spaceBetween: 30,
                    },
                    1600: {
                        slidesPerView: 6,
                        spaceBetween: 50,
                    },
                }}
            >
                {
                    (loadingProducts || productsData.length === 0 ? Array.from(new Array(4)) : productsData).map((product, index) => {
                        return (
                            <SwiperSlide key={index} onClick={() => singleProductFunc(product)}>
                                <ProductCard data={product} />
                            </SwiperSlide>
                        )
                    })
                }
            </Swiper>
        </div>
    )
}
