import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    isBasketBars: false ,
};

export const basketBarsSlice = createSlice({
  name: 'basketBars',
  initialState ,
  reducers: {
    setBasketBars: (state) => {
      state.isBasketBars = !state.isBasketBars;
    },
  }
})

// Action creators are generated for each case reducer function
export const { setBasketBars } = basketBarsSlice.actions

export default basketBarsSlice.reducer;