// import Skeleton from 'react-loading-skeleton'
// import 'react-loading-skeleton/dist/skeleton.css'
import stared from '../../assets/icons/stared.png'
import star from '../../assets/icons/star.png'
import ReactStars from "react-rating-stars-component";

export default function ReviewCard({ data, loading }) {

    const firstExample = {
        size: 25,
        value: loading ? 4 : data?.rating,
        edit: false,
        emptyIcon: <img src={star} />,
        filledIcon: <img src={stared} />,
    };

    return (
        <div className='card review bg-yellow-100'>
            <div className="card__body">
                <p className='text-base pb-20 text-center m-h-10'>
                    {data?.body}
                </p>
            </div>
            <div className='flex flex-wrap justify-center items-center gap-2 pb-2'>
                <div className='review__img'>
                    <img src={data?.image} />
                </div>
                <h4 className='text-md font-bold text-red-500'>
                    {data?.name}
                </h4>
            </div>
            <div className='flex flex-wrap gap-2 justify-center'>
                <ReactStars {...firstExample} />
            </div>
        </div>
    )
}
