import { LazyLoadImage } from 'react-lazy-load-image-component';
import Skeleton from 'react-loading-skeleton'
import { BASE_URL } from '../../api/api';
import Theme from '../skeleton/Theme';
import item1 from '../../assets/images/p-item-2.png'

export default function PopularCard({ data }) {
    return (
        <Theme>
            <div className='card popular text-center'>
                <div className='card__img text-center'>
                    <LazyLoadImage
                        alt={data?.name}
                        src={`${BASE_URL}${data?.image}`}
                        // src={item1}
                        effect="blur"
                    />
                </div>
                <div className="card__body">
                    <h4 className='text-md font-bold'>
                        {data?.name || <Skeleton />}
                    </h4>
                    <p className='text-sm pt-10 pb-25'>
                        {data?.description || <Skeleton />}
                    </p>
                    <h2 className='text-xl text-red-600 font-extrabold'>
                         {data?.sizes[0].price || data?.sizes[1].price || data?.sizes[2].price || <Skeleton width="20%" />}
                    </h2>
                </div>
            </div>
        </Theme>
    )
}
